import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

const initialState = {
    menu: {
		profile: false,
		main: false,
	},
	searchText: '',
};

@observer
class Navigation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    render() {
		const { appState } = this.props.stores;
		const { path } = appState;

		return (
			<div class='navbar-themed fixed-bottom'>
				<div class='container-fluid'>
					<div class='row'>
						<div class={`col-3 text-center p-2 ${path.match(/\/dogs/) ? 'border-bottom border-primary' : ''}`}>
							<a href='/dogs/' class={`text-${path.match(/\/dogs/) ? 'primary' : 'muted'}`}>
								<i class='fas fa-dog' style='font-size: 25px;' /><br />
								Hundene
							</a>
						</div>
						<div class={`col-3 text-center p-2 ${path.match(/\/workouts/) ? 'border-bottom border-primary' : ''}`}>
							<a href='/workouts/' class={`text-${path.match(/\/workouts/) ? 'primary' : 'muted'}`}>
								<i class='fas fa-running' style='font-size: 25px;' /><br />
								Treningen
							</a>
						</div>
						<div class={`col-3 text-center p-2 ${path.match(/\/teams/) ? 'border-bottom border-primary' : ''}`}>
							<a href='/teams/' class={`text-${path.match(/\/teams/) ? 'primary' : 'muted'}`}>
								<i class='fas fa-users' style='font-size: 25px;' /><br />
								Teamene
							</a>
						</div>
						<div class={`col-3 text-center p-2 ${path.match(/\/tracks/) ? 'border-bottom border-primary' : ''}`}>
							<a href='/tracks/' class={`text-${path.match(/\/tracks/) ? 'primary' : 'muted'}`}>
								<i class='fas fa-map-marked-alt' style='font-size: 25px;' /><br />
								Rundene
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Navigation;
