import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import mu from '../../lib/musher-util';

const DAYS = ['Sn', 'Man', 'Tir', 'Ons', 'Tor', 'Fre', 'Lr'];
const MONTHS = ['', 'Jan', 'Feb', 'Mar', 'Apr', 'Mai', 'Jun', 'Jul', 'Aug', 'Sep', 'Okt', 'Nov', 'Des'];

@observer
class WorkoutCalendar extends Component {

    async loadWorkoutSummary(props = this.props) {
        this.setState({ isLoading: true });
		const { userStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;

        const currentMonth = new Date().getMonth() + 1;
        const { month = currentMonth, year = util.getYear(), team = currentTeam, dog = {} } = props;
        await userStore.getWorkoutSummary({ team, dog: dog.id });
        await userStore.getWorkoutSummaryDay({ month, year, team, dog: dog.id });
        this.setState({ isLoading: false });
    }

    componentDidMount() {
        this.loadWorkoutSummary();
    }

    componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.month !== this.props.month
            || nextProps.year !== this.props.year
            || nextProps.dog !== this.props.dog
            || nextProps.team !== this.props.team) {
            this.loadWorkoutSummary(nextProps);
        }
    }

    render() {
        const { appState, userStore } = this.props.stores;
		const { darkmode } = appState;
        const currentMonth = new Date().getMonth() + 1;
		const { month = currentMonth, year = util.getYear(), dog, basehref } = this.props;

        const monthNum = parseInt(month, 10);
        const yearNum = parseInt(year, 10);
        const { month: nextMonth, year: nextYear } = mu.getNextMonth(monthNum, yearNum);
        const { month: prevMonth, year: prevYear } = mu.getPrevMonth(monthNum, yearNum);

        const daysOfMonth = util.getDaysInMonth(month, year);
        const weeksInMonth = util.getWeeksInMonth(month, year);
        const firstWeek = weeksInMonth[0].week;
        const daysOfWeek = util.getDaysInWeek(firstWeek, year);
        let totalDistanceKmMonth = 0;
        let totalCountMonth = 0;
        let totalElevationMonth = 0;
        let totalDurationMonth = 0;
        let totalSpeedavgMonth = [];


        return (
            <>
                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4'>
                    <a href={`${basehref || '/workouts/month/'}${nextMonth}/${nextYear}/skipScroll`} class='float-right btn btn-sm btn-link'><i class='fas fa-arrow-right' /></a>
                    <a href={`${basehref || '/workouts/month/'}${prevMonth}/${prevYear}/skipScroll`} class='float-left btn btn-sm btn-link'><i class='fas fa-arrow-left' /></a>
                    <h5>Mned - {MONTHS[month]} {year}</h5>
                    {dog && <div>{dog.name}</div>}
                    <small>{util.isoDate(daysOfMonth[0], false, false, true)} - {util.isoDate(daysOfMonth[daysOfMonth.length - 1], false, false, true)}</small>
                </div>

                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4'>
                    <table class='table table-striped table-sm'>
                        <thead>
                            <tr>
                                <th class='text-muted'><small>Uke</small></th>
                                {daysOfWeek.map(day => <th><small>{DAYS[day.getDay()]}</small></th>)}
                                <th class='text-muted'><small>Sum</small></th>
                                {/* <th class='text-muted'>Totalt</th> */}
                            </tr>
                        </thead>
                        <tbody>
                            {weeksInMonth && weeksInMonth.map(weekObj => {
                                const daysOfWeek = util.getDaysInWeek(weekObj.week, year);
                                let totalWeek = 0;
                                return (
                                    <tr>
                                        <td class='text-right text-muted pt-3'>
                                            <small>
                                                <a href={`/workouts/week/${weekObj.week}/${year}`} class='text-muted'>{weekObj.week}</a>
                                            </small>
                                        </td>
                                        {daysOfWeek.map(day => {
                                            const summary = userStore.findWorkoutSummaryWeekDay(day.getFullYear(), day.getMonth() + 1, day.getDate()) || {};
                                            if (summary && summary.distanceKm) {
                                                totalWeek += summary.distanceKm;
                                                totalDistanceKmMonth += summary.distanceKm;
                                                totalCountMonth += summary.count;
                                                totalElevationMonth += summary.elevation;
                                                totalDurationMonth += summary.duration;
                                                totalSpeedavgMonth.push(summary.speedAvg);
                                            }
                                            // console.log(summary);
                                            // count: 1, day: 5, distanceKm: 10, duration: 3600, elevation: 200, month: 9, speedAvg: 12, year: 2020
                                            return (
                                                <td class={`text-center font-weight-light position-relative pt-3`}
                                                    style={`background-color: ${mu.getDayColor(day.getDay(), darkmode)}; font-size: 0.8em;`}>
                                                    <div class='position-absolute text-muted' style='top: 0px; right: 2px; font-size: 0.7em;'>
                                                        {day.getDate()}
                                                    </div>
                                                    {summary && summary.distanceKm ? 
                                                        <div class='rounded-pill w-100 px-1'
                                                            style={`background-color: ${mu.getTrainingColor(summary, darkmode)};`}
                                                        >
                                                            {util.format(summary.distanceKm, 1)}
                                                            {summary.count > 1 && <span class='position-absolute text-danger' style='bottom: 0; right: 0px; font-size: 0.6em;'>{summary.count}</span>}
                                                        </div> : '-'}
                                                </td>
                                            );
                                        })}
                                        <td class='text-right pt-3' style='font-size: 0.8em;'>{util.format(totalWeek, 1)}</td>
                                        {/* <td class='text-right text-muted pt-3'>{totalDistanceKmMonth}</td> */}
                                    </tr>
                                );
                            })}
                        </tbody>
                        <tfoot>
                            <tr>
                                <td colspan='8' class='text-right'>Totalt km</td>
                                <td class='text-right text-muted'>{util.format(totalDistanceKmMonth, 1)}</td>
                            </tr>
                        </tfoot>
                    </table>

                    <h5 class='mt-4'>Oppsummering - {MONTHS[month]} {year}</h5>
                    {dog && <div>{dog.name}</div>}

                    <table class='table table-striped table-sm'>
                        <tbody>
                            <tr>
                                <td class='text-muted'>Antall kter</td>
                                <td class='text-right'>{totalCountMonth}</td>
                            </tr>
                            <tr>
                                <td class='text-muted'>Distanse</td>
                                <td class='text-right'>{util.format(totalDistanceKmMonth, 1)} km</td>
                            </tr>
                            <tr>
                                <td class='text-muted'>Hydemeter</td>
                                <td class='text-right'>{util.format(totalElevationMonth, 0)} m</td>
                            </tr>
                            <tr>
                                <td class='text-muted'>Varighet</td>
                                <td class='text-right'>{util.secToHms(totalDurationMonth)}</td>
                            </tr>
                            <tr>
                                <td class='text-muted'>Gjennomsnittsfart</td>
                                <td class='text-right'>{util.format(mu.avgArr(totalSpeedavgMonth), 1)} km/t</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </>
        );
    }
}

export default WorkoutCalendar;
