import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';

import Dog from '../../components/dog';
import DogAddLog from '../../components/dog/addlog';
import DogStatus from '../../components/dog/status';

const initialState = {
	isLoading: false,
};

@observer
class Teams extends Component {
    toggleTeam = (team) => {
		const { callback } = this.props;
        const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;
		if (team === currentTeam) {
			dogStore.setCurrentTeam(undefined);
		} else {
			dogStore.setCurrentTeam(team);
		}
		if (util.isDefined(callback)) {
			callback();
		}
	}

    render() {
        const { userStore, dogStore } = this.props.stores;
        const { teams } = userStore;
		const { currentTeam } = dogStore;
        return (
			<div class='container-fluid px-0'>
				<div class='row'>
					{teams && teams.map(team => (
						<div class='col-6 mb-2'>
							<div class={`border rounded rounded-pill px-3 py-2 ${currentTeam === team.id ? 'bg-success' : ''}`}
								style='line-height: 0.9em; cursor: pointer; pointer-events: inherit;'
								onClick={() => this.toggleTeam(team.id)}
							>
								<small>
									<i class='fas fa-users mr-2' />
									{team.name}
								</small>
							</div>
						</div>
					))}
				</div>
			</div>
        );
    }
}

@observer
class Dogs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	loadDogs = async () => {
		this.setState({ isLoading: true });
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
		this.setState({ isLoading: false });
	}

	componentDidMount() {
		this.loadDogs();
	}

	render() {
		const { isLoading } = this.state;
		const { dogStore, appState } = this.props.stores;
		const { darkmode } = appState;
		const { dogs } = dogStore;
		
		const dogsMaturity = dogStore.findDogsByStatus(10);
		const dogsWithStatus = dogStore.findDogsByStatus([2, 4, 8, 9]);
		const dogsWithNeeds = dogStore.findDogsWithNeeds();

		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 90px;'>
				<div class='row mb-4 fixed-top' style={`margin-top: 58px; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					<div class='col text-center'>
						<a href='/dogs' class='text-secondary stretched-link'><i class='fas fa-chart-line' /> Oversikt</a>
					</div>
					<div class='col text-center'>
						<a href='/dogs/list' class='text-secondary stretched-link'><i class='fas fa-list' /> Liste</a>
					</div>
					<div class='col text-center border-bottom border-primary'>
						<a href='/dogs/todo' class='text-primary stretched-link'><i class='fas fa-calendar-check' /> Todo</a>
					</div>
				</div>

				<div class='row'>
					<div class='col-12'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mb-2 border-bottom'>
							<Teams stores={this.props.stores} callback={this.loadDogs} />
						</div>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 pb-2 clearfix'>
							<span class='float-right'>
								<DogAddLog stores={this.props.stores} dog={{}} />
							</span>
						</div>
					</div>
				</div>

                <div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-4'>
						<h5>Hunder som trenger oppflging</h5>
						<div class='row'>
							{dogsWithStatus && dogsWithStatus.map(d => <DogStatus stores={this.props.stores} dog={d} />)}
							{!dogsWithStatus || dogsWithStatus.length === 0 && <div class='col-12 text-center text-muted'>
								<h3><i class='fas fa-thumbs-up' /></h3>
								Ingen hunder som trenger ekstra oppflging.
							</div>}
						</div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-4'>
						<h5>Hunder med spesielle behov</h5>
						<div class='row'>
							{dogsWithNeeds && dogsWithNeeds.map(d => <DogStatus stores={this.props.stores} dog={d} />)}
							{!dogsWithNeeds || dogsWithNeeds.length === 0 && <div class='col-12 text-center text-muted'>
								<h3><i class='fas fa-thumbs-up' /></h3>
								Ingen hunder med spesielle behov.
							</div>}
						</div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-4'>
						<h5>Hunder med lpetid</h5>
						<div class='row'>
							{dogsMaturity && dogsMaturity.map(d => <DogStatus stores={this.props.stores} dog={d} />)}
							{!dogsMaturity || dogsMaturity.length === 0 && <div class='col-12 text-center text-muted'>
								<h3><i class='fas fa-thumbs-up' /></h3>
								Ingen hunder med lpetid.
							</div>}
						</div>
					</div>					

				</div>
			</div>
		);
	}
}

export default Dogs;
