import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';

const MONTHS = ['jan', 'feb', 'mar', 'apr', 'mai', 'jun', 'jul', 'aug', 'sep', 'okt', 'nov', 'des'];

@observer
class WorkoutGraph extends Component {
    render() {
        const { userStore } = this.props.stores;
        const { isLoading } = this.props;
        const {
			workoutSummary,
			graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev,
			graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev,
		} = userStore;

        return (
            <>
                <h5>Trenings statistikk</h5>
                <div class='w-100 mt-3 mb-4'>
                    <small class='mb-4'><small>
                        <table class='table table-sm font-weigth-lighter border-bottom'>
                            <thead>
                                <tr>
                                    <th style='padding: .2rem;'>Mnd</th>
                                    {util.range(1, 12).map(m => (
                                        <th class='text-right' style='padding: .2rem;'><a href={`/workouts/month/${m}`}>{MONTHS[m - 1]}</a></th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <th scope='row' style='padding: .2rem;'>2019</th>
                                    {util.range(1, 12).map(m => (
                                        <>
                                            <td class='text-right' style='padding: .2rem;'>
                                                <nobr>{userStore.findWorkoutSummaryMonth(2019, m)}</nobr><br />
                                                <nobr>{userStore.findWorkoutSummaryMonth(2019, m, 'elevation')}</nobr><br />
                                            </td>
                                        </>
                                    ))}
                                    <td class='text-muted' style='padding: .2rem;'>
                                        <nobr><i class='fas fa-road' /> km</nobr><br />
                                        <nobr><i class='fas fa-mountain' /> m</nobr><br />
                                    </td>
                                </tr>
                                <tr>
                                    <th scope='row' style='padding: .2rem;'>2020</th>
                                    {util.range(1, 12).map(m => (
                                        <>
                                            <td class='text-right' style='padding: .2rem;'>
                                                <nobr>{userStore.findWorkoutSummaryMonth(2020, m)}</nobr><br />
                                                <nobr>{userStore.findWorkoutSummaryMonth(2020, m, 'elevation')}</nobr><br />
                                            </td>
                                        </>
                                    ))}
                                    <td class='text-muted' style='padding: .2rem;'>
                                        <nobr><i class='fas fa-road' /> km</nobr><br />
                                        <nobr><i class='fas fa-mountain' /> m</nobr><br />
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </small></small>
                                
                    <LineGraph
                        stores={this.props.stores}
                        width={500}
                        height={150}
                        showXTicks={1}
                        xTicksVal={val => `uke ${val}`}
                        paddingTop={30}
                        lineFromXzero={true}
                        lineFromXmax={true}
                        xTicksLink={'/workouts/week/'}

                        dataLeft={[graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev]}
                        legendLeft={['2020 Distanse', '2019 Distanse']}
                        // yMaxLeft={30}
                        // yMinLeft={0}
                        showYTicksLeft={1}
                        yTicksLeftPostfix={'km'}
                        unitLeft={['km', 'km']}
                        avgWindowLeft={[0, 0]}
                        smoothLeft={[1, 1]}

                        dataRight={[graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev]}
                        legendRight={['2020 Hydemeter', '2019 Hydemeter']}
                        showYTicksRight={1}
                        yTicksRightPostfix={'m'}
                        unitRight={['m', 'm']}
                        avgWindowRight={[0, 0]}
                        smoothRight={[1, 1]}

                        // marker={week}
                    />
                </div>

                {!isLoading && workoutSummary && workoutSummary.length < 3 && (
                    <div class='mb-5 py-3 border-top border-bottom'>
                        <div class='text-left' style='line-height: 1.0em;'>
                            <div class='display-4 float-left text-warning mr-3'>
                                <i class='fas fa-paw' />
                            </div>
                            <small>
                                Forelpig er det litt lite data for  kunne vise noen fine grafer. 
                                Etterhvert som du legger inn flere treninger s vil dette bli en fin tabell og graf som viser progresjonen din.
                            </small>
                        </div>
                    </div>
                )}
            </>
        );
    }
}

export default WorkoutGraph;
