import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';

import WorkoutGraph from '../../components/workout/graph';
import WorkoutCalendar from '../../components/workout/calendar';
import DogHistory from '../../components/dog/history';
import DogAddLog from '../../components/dog/addlog';
import DogCalendar from '../../components/dog/calendar';
import Modal from '../../components/modal';

function byDate(a, b) {
	if (a.date < b.date){
		return -1;
	}
	if (a.date > b.date){
		return 1;
	}
	return 0;
}

const initialState = {};

@observer
class DogsDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
			newHistory: {
				date: util.isoDate(new Date(), false, false, true),
				time: util.isoTime(new Date()),
			},
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadDog() {
		const { dogStore, userStore } = this.props.stores;
        await dogStore.load(this.props.dog);
		const { dog } = dogStore;
        await userStore.getWorkoutSummary({ dog: dog.id });
    }

	
	showModal = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const img = e.target.dataset.image;
        const imgXXL = e.target.dataset.imagexxl;
		const thumb = e.target.dataset.thumb;
        const name = e.target.dataset.name;
        const title = e.target.dataset.title;
        const date = e.target.dataset.date;
        this.setState({
            displayModal: true,
            currentImage: img,
            currentImageXXL: imgXXL,
            thumb,
            name,
            title,
            date,
        });
    }

    closeModal = () => {
        this.setState({
            displayModal: false,
        });
    }

    setHistoryType = (historyType) => {
		this.setState({ historyType });
    }

    back = () => {
        route(`/dogs/`, true);
    }

	componentDidMount() {
        this.loadDog();
    }

	render() {
		const { displayModal, currentImage, currentImageXXL, thumb, name, title, date, historyType } = this.state;
        const { month, year } = this.props;
        // const monthNum = parseInt(month, 10);
        // const yearNum = parseInt(year, 10);
        const filterHistories = historyType ? (e) => {
            // if (monthNum && yearNum) {
            //     return e.historyType === historyType && e.month === monthNum && e.year === yearNum;
            // }
            return e.historyType === historyType;
        } : () => true;
		const { dogStore, userStore } = this.props.stores;
        const { historyFeed } = userStore;
		const hasHistory = historyFeed && historyFeed.length > 0;
    
		const { dog } = dogStore;
        const workoutSummary2019 = dogStore.findWorkoutSummary(dog.id, 2019) || {};
        const workoutSummary2020 = dogStore.findWorkoutSummary(dog.id, 2020) || {};
        const dogStatus = userStore.findDogstatus(dog.status);

		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <div class='d-flex justify-content-between mb-2'>
                    <div>
                        <button class='btn btn-link pl-0 fixed-top'
                            style='top: 50px; left: 5px; font-size: 25px; width: 40px;'
                            onClick={this.back}>
                            <i class='fas fa-arrow-circle-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>&nbsp;</h5>
                    </div>
                    <div>
                        <a class='btn btn-sm btn-primary float-right ml-2'
							href={`/dogs/edit/${dog.id}`}
						>
							<i class='fas fa-edit' /> Endre
						</a>
                        <DogAddLog stores={this.props.stores} dog={dog} />
                    </div>
                </div>

				{displayModal && <Modal close={this.closeModal} image={currentImage} imagexxl={currentImageXXL} thumb={thumb} name={name} title={title} date={date} />}

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<div class='d-flex mb-5'>
                            {/* {JSON.stringify(dog, null, 4)} */}
                            <div class={`text-center border rounded-circle imageRounded`} style={dog.image ? `background-image: url("${dog.image.s3SmallLink}");` : ''}>
                                {!dog.image && <i class='fas fa-dog text-muted mt-3' style='font-size: 40px;' />}
                            </div>
                            <div class='flex-grow-1 pl-3 pr-2'
								style={`border-right: 4px solid ${userStore.findHarness(dog.harness, 'colorNonstop')} !important;`}
							>
								<span class='float-right'>
									{dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)}</span>}
									{dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
								</span>
								<h5>{dog.name}</h5>
								{dog.shortname}<br />

								<div>
									{dog.chipId && <small><i class='fas fa-microchip' /> {dog.chipId}</small>}
								</div>

                                <div>
                                    {dog.specialNeeds && <span class={`badge badge-warning mr-2`}><i class='fas fa-exclamation-triangle' /> {dog.specialNeeds}</span>}
                                    {dogStatus && <span class={`badge badge-${dogStatus.class} mr-2`}>{dogStatus.icon && <i class={dogStatus.icon} />} {dogStatus.name}</span>}
                                </div>

                            </div>
                        </div>

						<WorkoutGraph stores={this.props.stores} isLoading={false} />

						<WorkoutCalendar stores={this.props.stores} dog={dog} basehref={`/dogs/${dog.id}/`} month={month} year={year} />

						<div class='container-fluid mb-5 mt-4'>
							<div class='row border-top'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-road' /> 2020-21</small>
                                    <h3>
										{util.format(workoutSummary2020.distanceKm || 0, 0)} km
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-road' /> 2019-20</small>
                                    <h3>
                                        {util.format(workoutSummary2019.distanceKm || 0, 0)} km
                                    </h3>
                                </div>
                            </div>
							<div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> 2020-21</small>
                                    <h3>
										{util.format(workoutSummary2020.elevation || 0, 0)} m
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> 2019-20</small>
                                    <h3>
                                        {util.format(workoutSummary2019.elevation || 0, 0)} m
                                    </h3>
                                </div>
                            </div>
							<div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-clock' /> 2020-21</small>
                                    <h3>
										{util.secToHms(workoutSummary2020.duration || 0)}
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-clock' /> 2019-20</small>
                                    <h3>
                                        {util.secToHms(workoutSummary2019.duration || 0)}
                                    </h3>
                                </div>
                            </div>
							<div class='row mt-2 border-bottom'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-tachometer-alt' /> 2020-21</small>
                                    <h3>
										{util.format(workoutSummary2020.speedAvg || 0, 1)} km/t
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-tachometer-alt' /> 2019-20</small>
                                    <h3>
                                        {util.format(workoutSummary2019.speedAvg || 0, 1)} km/t
                                    </h3>
                                </div>
                            </div>

	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'>Fdt</small>
                                    <h3>
                                        {util.isoDate(dog.birth, false, false, true)}
                                    </h3>
                                </div>
                                {dog.deceased && <div class='col text-center border-left'>
                                    <small class='text-muted'>Dd</small>
                                    <h3>
                                        {util.isoDate(dog.deceased, false, false, true)}
                                    </h3>
                                </div>}
                            </div>
	                        <div class='row mt-2'>
								<div class='col text-center'>
                                    <small class='text-muted'>Team</small>
                                    <h3>
                                        {dog.team && userStore.findTeam(dog.team)}
                                    </h3>
                                </div>
							</div>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'>Mor</small>
                                    <h3>
                                        {dog.mother}
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'>Far</small>
                                    <h3>
                                        {dog.father}
                                    </h3>
                                </div>
                            </div>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'>Ssken</small>
                                    <h3>
                                        {dog.siblings}
                                    </h3>
                                </div>
                            </div>
                        </div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<div class='container-fluid border-bottom mb-5'>
							<div class='row'>
								<div class='col px-0'>
									<h5>Bilder av {dog.name}</h5>
								</div>
							</div>
							<div class='row'>
								{dog.images && dog.images.map(img => (
									<div class={`text-center border rounded imageRounded`} style={img ? `background-image: url("${img.s3SmallLink}");` : ''}
										onClick={this.showModal}
										data-image={img.s3LargeLink}
										data-imagexxl={img.s3XXLargeLink}
										data-thumb={dog.image.s3SmallLink}
										data-name={dog.name}
									>					
										&nbsp;
									</div>
								))}
							</div>
						</div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-2 mt-3'>
						<DogAddLog stores={this.props.stores} dog={dog} />
						<a class='btn btn-sm btn-primary float-right'
							href={`/dogs/edit/${dog.id}`}
						>
							<i class='fas fa-edit' /> Endre {dog.name}
						</a>
					</div>

					<div class='offset-0 col-12 mt-5'>
						<div class='container-fluid'>
							<div class='row'>
								<div class='col-12'>
									<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
										<h5 class='mt-3'>Logg for {dog.name}</h5>
									</div>

            						<DogCalendar stores={this.props.stores} basehref={`/dogs/${dog.id}/history/`} dog={dog} month={month} year={year} callback={this.setHistoryType} />

									{!hasHistory && <div class='text-muted'>Loggen til {dog.name} er tom</div>}
									{hasHistory && historyFeed.filter(filterHistories).map(line => (
										<DogHistory stores={this.props.stores} line={line} />
									))}							
								</div>
							</div>
						</div>

					</div>
				</div>
			</div>
		);
	}
}

export default DogsDetail;
