import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import mu from '../../lib/musher-util';

import Workout from '../../components/workout';
import WorkoutGraph from '../../components/workout/graph';

const DAYS = ['Sn', 'Man', 'Tir', 'Ons', 'Tor', 'Fre', 'Lr'];

const initialState = {
	isLoading: false,
};

@observer
class Teams extends Component {
    toggleTeam = (team) => {
		const { callback } = this.props;
        const { workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		if (team === currentTeam) {
			workoutStore.setCurrentTeam(undefined);
		} else {
			workoutStore.setCurrentTeam(team);
		}
		if (util.isDefined(callback)) {
			callback();
		}
	}

    render() {
        const { userStore, workoutStore } = this.props.stores;
        const { teams } = userStore;
		const { currentTeam } = workoutStore;
        return (
			<div class='container-fluid px-0'>
				<div class='row'>
					{teams && teams.map(team => (
						<div class='col-6 mb-2'>
							<div class={`border rounded rounded-pill px-3 py-2 ${currentTeam === team.id ? 'bg-success' : ''}`}
								style='line-height: 0.9em; cursor: pointer; pointer-events: inherit;'
								onClick={() => this.toggleTeam(team.id)}
							>
								<small>
									<i class='fas fa-users mr-2' />
									{team.name}
								</small>
							</div>
						</div>
					))}
				</div>
			</div>
        );
    }
}

@observer
class Workouts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	loadWorkouts = async () => {
		this.setState({ isLoading: true });
		// const { workoutStore } = this.props.stores;
		// const { currentTeam } = workoutStore;
		// await workoutStore.load({ query: { team: currentTeam } });
		await this.loadWorkoutSummary();
		this.setState({ isLoading: false });
	}

	loadDogs = async () => {
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
	}

	async loadTracks() {
		const { trackStore } = this.props.stores;
		await trackStore.load();
	}

	async loadWorkoutSummary(props = this.props) {
		const { userStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
        const { week, year } = props;
        await userStore.getWorkoutSummary({ team: currentTeam });
        await userStore.getWorkoutSummaryDay({ week, year, team: currentTeam });
    }

    componentDidMount() {
		this.loadWorkouts();
		// this.loadDogs();
		// this.loadTracks();
    }

    componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.week !== this.props.week) {
            this.loadWorkoutSummary(nextProps);
        }
    }

	render() {
		const { isLoading } = this.state;
		const { userStore, appState } = this.props.stores;
		const { darkmode } = appState;
		const { week, year = util.getYear() } = this.props;
		const {
            workoutsDay,
		} = userStore;

        const daysOfWeek = util.getDaysInWeek(week, year);
        let totalDistanceKmWeek = 0;
        let totalCountWeek = 0;
        let totalElevationWeek = 0;
        let totalDurationWeek = 0;
        let totalSpeedavgWeek = [];


        const weekNum = parseInt(week, 10);
        const yearNum = parseInt(year, 10);
        const { week: nextWeek, year: nextYear } = mu.getNextWeek(weekNum, yearNum);
        const { week: prevWeek, year: prevYear } = mu.getPrevWeek(weekNum, yearNum);

		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 90px;'>
				<div class='row mb-4 fixed-top' style={`margin-top: 58px; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					<div class='col text-center'>
						<a href='/workouts/' class='text-secondary stretched-link'><i class='fas fa-chart-line' /> Oversikt</a>
					</div>
					<div class='col text-center'>
						<a href='/workouts/list' class='text-secondary stretched-link'><i class='fas fa-list' /> Liste</a>
					</div>
					<div class='col text-center'>
						<a href='/workouts/plan' class='text-secondary stretched-link'><i class='fas fa-calendar-alt' /> Plan</a>
					</div>
				</div>
				
				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
						<Teams stores={this.props.stores} callback={this.loadWorkouts} />
					</div>

                    {/* <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<WorkoutGraph stores={this.props.stores} isLoading={isLoading} />
					</div> */}

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4'>
                        <a href={`/workouts/week/${nextWeek}/${nextYear}`} class='float-right btn btn-sm btn-link'><i class='fas fa-arrow-right' /></a>
                        <a href={`/workouts/week/${prevWeek}/${prevYear}`} class='float-left btn btn-sm btn-link'><i class='fas fa-arrow-left' /></a>
                        <h5>Uke - {week} {year}</h5>
                        <small>
                            <a href={`/workouts/month/${daysOfWeek[0].getMonth() + 1}/${daysOfWeek[0].getFullYear()}`}>
                                {util.isoDate(daysOfWeek[0], false, false, true)}
                            </a> - <a href={`/workouts/month/${daysOfWeek[daysOfWeek.length - 1].getMonth() + 1}/${daysOfWeek[daysOfWeek.length - 1].getFullYear()}`}>
                                {util.isoDate(daysOfWeek[daysOfWeek.length - 1], false, false, true)}
                            </a>
                        </small>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4'>

                        <table class='table table-striped table-sm'>
                            <thead>
                                <tr>
                                    <th class='text-muted'>Uke</th>
                                    {daysOfWeek.map(day => <th>{DAYS[day.getDay()]}</th>)}
                                    <th class='text-muted'>Sum</th>
                                    {/* <th class='text-muted'>Totalt</th> */}
                                </tr>
                            </thead>

                            <tbody>
                                <tr>
                                    <td class='text-right text-muted pt-3'>{week}</td>
                                    {daysOfWeek.map(day => {
                                        const summary = userStore.findWorkoutSummaryWeekDay(day.getFullYear(), day.getMonth() + 1, day.getDate()) || {};
                                        if (summary && summary.distanceKm) {
                                            totalDistanceKmWeek += summary.distanceKm;
                                            totalCountWeek += summary.count;
                                            totalElevationWeek += summary.elevation;
                                            totalDurationWeek += summary.duration;
                                            totalSpeedavgWeek.push(summary.speedAvg);
                                        }
                                        // console.log(summary);
                                        // count: 1, day: 5, distanceKm: 10, duration: 3600, elevation: 200, month: 9, speedAvg: 12, year: 2020
                                        return (
                                            <td class={`text-center font-weight-light position-relative pt-3`}
                                                style={`background-color: ${mu.getDayColor(day.getDay(), darkmode)};`}>
                                                <div class='position-absolute text-muted' style='top: 0px; right: 2px; font-size: 0.7em;'>
                                                    {day.getDate()}
                                                </div>
                                                {summary && summary.distanceKm ? 
                                                    <div class='rounded-pill w-100 px-1'
                                                        style={`background-color: ${mu.getTrainingColor(summary, darkmode)}`}
                                                    >
                                                        {summary.distanceKm}
                                                        {summary.count > 1 && <span class='position-absolute text-danger' style='bottom: 0; right: 0px; font-size: 0.6em;'>{summary.count}</span>}
                                                    </div> : '-'}
                                            </td>
                                        );
                                    })}
                                    <td class='text-right pt-3'>{totalDistanceKmWeek}</td>
                                    {/* <td class='text-right text-muted pt-3'>{totalWorkoutKm}</td> */}
                                </tr>
                            </tbody>
                        </table>

                        <h5 class='mt-4'>Oppsummering - {week} {year}</h5>
                        <table class='table table-striped table-sm'>
                            <tbody>
                                <tr>
                                    <td class='text-muted'>Antall kter</td>
                                    <td class='text-right'>{totalCountWeek}</td>
                                </tr>
                                <tr>
                                    <td class='text-muted'>Distanse</td>
                                    <td class='text-right'>{util.format(totalDistanceKmWeek, 1)} km</td>
                                </tr>
                                <tr>
                                    <td class='text-muted'>Hydemeter</td>
                                    <td class='text-right'>{util.format(totalElevationWeek, 0)} m</td>
                                </tr>
                                <tr>
                                    <td class='text-muted'>Varighet</td>
                                    <td class='text-right'>{util.secToHms(totalDurationWeek)}</td>
                                </tr>
                                <tr>
                                    <td class='text-muted'>Gjennomsnittsfart</td>
                                    <td class='text-right'>{util.format(mu.avgArr(totalSpeedavgWeek), 1)} km/t</td>
                                </tr>
                            </tbody>
                        </table>

                    </div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4'>
                        <h5>Treningsturene i uke - {week} {year}</h5>
                        {workoutsDay && workoutsDay.map(workout => (
							<Workout stores={this.props.stores} workout={workout} showDate={true} />
						))}
					</div>

				</div>
			</div>
		);
	}
}

export default Workouts;
