import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Workout from '../../components/workout';
import WorkoutSummary from '../../components/workout/summary';
import DogHistory from '../../components/dog/history';
import DogAddLog from '../../components/dog/addlog';
import DogStatus from '../../components/dog/status';

const MONTHS = ['jan', 'feb', 'mar', 'apr', 'mai', 'jun', 'jul', 'aug', 'sep', 'okt', 'nov', 'des'];

const initialState = {
	isLoading: false,
};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadAll() {
		this.setState({ isLoading: true });
		await this.loadDogs();
		this.setState({ isLoading: false });
    }

	loadDogs = async () => {
		this.setState({ isLoading: true });
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
		this.setState({ isLoading: false });
	}

	// componentWillUnmount() {
	//     PubSub.unsubscribe(subscription);
	// }
    componentDidMount() {
        this.loadAll();
    }

	render() {
		const { dogStore, appState } = this.props.stores;
        const { darkmode } = appState;

		const dogsMaturity = dogStore.findDogsByStatus(10);
		const dogsWithStatus = dogStore.findDogsByStatus([2, 4, 8, 9]);
		const dogsWithNeeds = dogStore.findDogsWithNeeds();

		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 90px;'>
				<div class='row mb-4 fixed-top' style={`margin-top: 58px; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					<div class='col text-center'>
						<a href='/' class='text-secondary stretched-link'><i class='fas fa-chart-line' /> Dashboard</a>
					</div>
					<div class='col text-center'>
						<a href='/feed' class='text-secondary stretched-link'><i class='fas fa-rss' /> Feed</a>
					</div>
					<div class='col text-center border-bottom border-primary'>
						<a href='/todo' class='text-primary stretched-link'><i class='fas fa-list' /> Todo</a>
					</div>
				</div>

				<div class='row'>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-4'>
						<h5>Hunder som trenger oppflging</h5>
						<div class='row'>
							{dogsWithStatus && dogsWithStatus.map(d => <DogStatus stores={this.props.stores} dog={d} />)}
							{!dogsWithStatus || dogsWithStatus.length === 0 && <div class='col-12 text-center text-muted'>
								<h3><i class='fas fa-thumbs-up' /></h3>
								Ingen hunder som trenger ekstra oppflging.
							</div>}
						</div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-4'>
						<h5>Hunder med spesielle behov</h5>
						<div class='row'>
							{dogsWithNeeds && dogsWithNeeds.map(d => <DogStatus stores={this.props.stores} dog={d} />)}
							{!dogsWithNeeds || dogsWithNeeds.length === 0 && <div class='col-12 text-center text-muted'>
								<h3><i class='fas fa-thumbs-up' /></h3>
								Ingen hunder med spesielle behov.
							</div>}
						</div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-4'>
						<h5>Hunder med lpetid</h5>
						<div class='row'>
							{dogsMaturity && dogsMaturity.map(d => <DogStatus stores={this.props.stores} dog={d} />)}
							{!dogsMaturity || dogsMaturity.length === 0 && <div class='col-12 text-center text-muted'>
								<h3><i class='fas fa-thumbs-up' /></h3>
								Ingen hunder med lpetid.
							</div>}
						</div>
					</div>					

				</div>
			</div>
		);
	}
}

export default Home;
